import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	data() {
		return {
			type: '',
			del_id:'',
			form: {
				name: '',
				mobile: '',
				city: '',
				detail: '',
				province: '',
				city: '',
				county: '',
				detail: '',
				areacode:''
			},
			tabIndex: 0,
			mode: "range",
			tabList: [{
				mode: "region",
				name: ""
			}],
			resultInfo: {
				result: '请选择地区'
			}
		}
	},
	onLoad(option) {
		console.log(option)
		if (option.type == 'edit') {
			let cache = uni.getStorageSync('edit_data')
			this.form = cache
			this.type = 'edit'
			this.del_id = cache.id
		}
		else if (option.type == 'add') {
			this.type = 'add'
		}
	},
	components: {
		wPicker
	},
	onShow() {

	},
	methods: {
		del(){
			this.$api.http.put('address/del_address',{id:this.del_id}).then(res=>{
				this.$api.msg('删除成功');
				setTimeout(() => {
					uni.navigateBack()
				}, 1000)
			})
		},
		toggleTab() {
			this.$refs['region'].show();
		},
		onConfirm(val) {
			console.log(val);
			//如果页面需要调用多个mode类型，可以根据mode处理结果渲染到哪里;
			// switch(this.mode){
			// 	case "date":
			// 		break;
			// }
			this.resultInfo = val;
			// this.form.city = this.resultInfo.result
			this.form.province = val.checkArr[0]
			this.form.city = val.checkArr[1]
			this.form.county = val.checkArr[2]
			console.log(this.form)
		},

		sub_edit(id) {
			const that = this
			const data = this.form
			this.$api.http.post('address/edit_address', data).then(res => {
				this.$api.msg('修改成功');
				setTimeout(() => {
					uni.navigateBack()
				}, 1000)
			})

		},
		sub_add() {
			const data = this.form
			if(data.mobile.length != 11){
				this.$api.msg('请输入正确的的手机号')
				return
			}
			this.$api.http.post('address/add_address', data).then(res => {
				this.$api.msg('添加成功');
				setTimeout(() => {
					uni.navigateBack()
				}, 1000)
			})
		}

	}
}
